* {
    box-sizing: border-box;
    
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  
}

.topnav {
  overflow: hidden;
  background-color: #ffffff;
  
}

.topnav a {
  float: left;
  display: block;
  color: black;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
  
}

.topnav a:hover {
  background-color: #D7D7D7;
  color: black;
  
}

.topnav a.active {
  background-color: #FFC529;
  color: rgb(0, 0, 0);
  
}

.topnav .search-container {
  float: right;
  
}

.topnav input[type=text] {
  padding: 6px;
  margin-top: 8px;
  margin-right: 3px;
  font-size: 17px;
  border: none;
  background: #D7D7D7;
  border-radius: 25px;
}

.topnav .search-container button {
  float: right;
  padding: 6px 10px;
  margin-top: 8px;
  margin-right: 16px;
  background: #FFC529;
  font-size: 17px;
  border: none;
  cursor: pointer;
  border-radius: 50%;
}

.topnav .search-container button:hover {
  background: #D7D7D7;
}

@media screen and (max-width: 600px) {
  .topnav .search-container {
    float: none;
  }
  .topnav a, .topnav input[type=text], .topnav .search-container button {
    float: none;
    display: block;
    text-align: left;
    width: 100%;
    margin: 0;
    padding: 14px;
  }
  .topnav input[type=text] {
    border: 1px solid #ccc;  
  }
}